import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'lib-currency-control',
  templateUrl: './currency-control.component.html',
  styleUrls: ['./currency-control.component.css']
})
export class CurrencyControlComponent implements OnInit {
  @Input() element;
  @Input() modelFields;
  @Input() dataView;
  @Input() detail;
  @Input() submit = false;
  classInput = false;
  @Output() callbackCurrency = new EventEmitter<any>();
  constructor() { }

  ngOnInit(): void {
  }

  inputFocus(event) {
    if (!this.element.columnValue) {
      this.classInput = true;
    }
  }

  inputFocusOut(event) {
    if (this.element.columnValue) {
      this.classInput = true;
    } else {
      this.classInput = false;
    }
  }

  onChangeValue(event, field_name, element) {
    this.callbackCurrency.emit({
      event: element,
      value: event.target.value,
      field: field_name
    })
  }

}
