import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
@Component({
  selector: 'lib-datetimes-control',
  templateUrl: './datetimes-control.component.html',
  styleUrls: ['./datetimes-control.component.css']
})
export class DatetimesControlComponent implements OnInit {
  @Input() element;
  @Input() modelFields;
  @Input() dataView;
  @Input() detail;
  @Input() submit = false;
  @Output() callbackDatetimes = new EventEmitter<any>();
  constructor() { }
  classInput = false;
  ngOnInit(): void {
  }

  onChangeValue(value, field_name, element) {
    this.modelFields[field_name].error = this.modelFields[field_name].isRequire && !this.element.columnValue ? true : false;
    this.modelFields[field_name].message = this.modelFields[field_name].error ? 'Trường bắt buộc nhập !' : ''
    this.callbackDatetimes.emit({
      event: element,
      value: value,
      field: field_name
    })
  }

  inputFocus(event) {
    if (!this.element.columnValue) {
      this.classInput = true;
    }
  }

  inputFocusOut(event) {
    if (this.element.columnValue) {
      this.classInput = true;
    } else {
      this.classInput = false;
    }
  }

}
