import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ValidationEmail, ValidationEmailEmpty, ValidationPhoneNumber, ValidationPhoneNumberEmpty, ValidationTaiKhoanNganHang, ValidationTaiKhoanNganHangEmpty } from '../validation';

@Component({
  selector: 'lib-text-control',
  templateUrl: './text-control.component.html',
  styleUrls: ['./text-control.component.css']
})
export class TextControlComponent implements OnInit {

  constructor() { }
  classInput = false;
  @Input() element;
  @Input() dataView;
  @Input() modelFields;
  @Input() detail;
  @Input() submit = false;
  @Output() callbackText = new EventEmitter<any>();
  ngOnInit(): void {
  }

  inputFocus(event) {
    if (!this.element.columnValue) {
      this.classInput = true;
    }
  }

  inputFocusOut(event) {
    if (this.element.columnValue) {
      this.classInput = true;
    } else {
      this.classInput = false;
    }
  }

  onChangeValue(event, field_name, element) {
     this.callbackText.emit({
      event: element,
      value: event.target.value,
      field: field_name
    })
  }

  searchCustomer() {
    this.callbackText.emit({
      event: null,
      value: null,
      field: null,
      type: 'Action'
    })
  }

}
