import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'lib-dropdown-control',
  templateUrl: './dropdown-control.component.html',
  styleUrls: ['./dropdown-control.component.css']
})
export class DropdownControlComponent implements OnInit {
  @Input() element;
  @Input() modelFields;
  @Input() dataView;
  @Input() detail;
  @Input() submit = false;
  @Output() callbackDropdown = new EventEmitter<any>();
  constructor() { }
  classInput = false;
  ngOnInit(): void {
  }

  onChangeValue(value, field_name, element) {
    this.modelFields[field_name].error = this.modelFields[field_name].isRequire && !this.element.columnValue ? true : false;
    this.modelFields[field_name].message = this.modelFields[field_name].error ? 'Trường bắt buộc nhập !' : ''
    this.callbackDropdown.emit({
      event: element,
      value: value,
      field: field_name
    })
  }

  inputFocus(event) {
    if (!this.element.columnValue) {
      this.classInput = true;
    }
  }

  inputFocusOut(event) {
    if (this.element.columnValue) {
      this.classInput = true;
    } else {
      this.classInput = false;
    }
  }

}
