import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'lib-multi-select-control',
  templateUrl: './multi-select-control.component.html',
  styleUrls: ['./multi-select-control.component.css']
})
export class MultiSelectControlComponent implements OnInit {

  constructor() { }
  classInput = false;
  @Input() element;
  @Input() dataView;
  @Input() modelFields;
  @Input() detail;
  @Input() submit = false;
  @Output() callbackMultiSelect = new EventEmitter<any>();
  ngOnInit(): void {
  }

  inputFocus(event) {
    if (!this.element.columnValue) {
      this.classInput = true;
    }
  }

  inputFocusOut(event) {
    if (this.element.columnValue) {
      this.classInput = true;
    } else {
      this.classInput = false;
    }
  }

  onChangeValue(event, field_name, element) {
     this.callbackMultiSelect.emit({
      event: element,
      value: event.value,
      field: field_name
    })
  }

}
