import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'lib-select-tree-control',
  templateUrl: './select-tree-control.component.html',
  styleUrls: ['./select-tree-control.component.css']
})
export class SelectTreeControlComponent implements OnInit {

  constructor() { }
  classInput = false;
  @Input() element;
  @Input() dataView;
  @Input() modelFields;
  @Input() detail;
  @Input() submit = false;
  @Output() callbackSelectTree = new EventEmitter<any>();
  ngOnInit(): void {
  }

  
  inputFocus(event) {
    if (!this.element.columnValue) {
      this.classInput = true;
    }
  }

  inputFocusOut(event) {
    if (this.element.columnValue) {
      this.classInput = true;
    } else {
      this.classInput = false;
    }
  }

  onChangeValue(event, field_name, element) {
     this.callbackSelectTree.emit({
      event: element,
      value: event.target.value,
      field: field_name
    })
  }


}
